export default {
	name: "PutItem",
	props: {
		list:null
	},
	data() {
		return {
			active:false
		}
	},
	computed: {
		getTypeText() {
			if(this.list.type === 0) {
				return '支付宝'
			}else if(this.list.type === 1) {
				return '微信'
			}else if(this.list.type === 2) {
				return '中国银行'
			}
		},
		getImg() {
			if(this.list.type === 0) {
				return '../../../../../static/img/putlive/zfb.png'
			}else if(this.list.type === 1) {
				return '../../../../../static/img/putlive/wx.png'
			}else if(this.list.type === 2) {
				return '../../../../../static/img/putlive/yl.png'
			}
		}
	}
	//0:支付宝  1:微信  2:银行卡
}
